import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ProjectService {

  constructor(private http: Http) { }

  getProject(){
    let headers = new Headers();
    //headers.append('Content-Type', 'application/json');
    //for dev use
    //'http://localhost:3000/project'

    //for deploy
    //'project'
    headers.append('Content-Type', 'application/json');
    return this.http.get('project', {headers: headers})
      .map(res => res.json());
  }
  
  postProject(project){
    //for dev use
    //'http://localhost:3000/project'

    //for deploy
    //'project'
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('project/project-create', project, {headers: headers})
      .map(res => res.json());
  }

  updateProject(id, project){
    //for dev use
    //'http://localhost:3000/project'

    //for deploy
    //'project'
    return this.http.put('project/'+ id, project)
      .map(res => res.json());
  }

  deleteProject(id){
    //for dev use
    //'http://localhost:3000/project'

    //for deploy
    //'project'
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.delete('project/'+ id, {headers: headers})
      .map(res => res.json());
  }
  getProjectById(id){
    //for dev use
    //'http://localhost:3000/project'

    //for deploy
    //'project'
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('project/'+id, {headers: headers})
      .map(res => res.json());
  }

}



